@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

html, body, #root {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Press Start 2P', sans-serif;
  background-color: #f7e39c;
}

.App {
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
  margin: 0 auto;
  border: 5px solid #3a5f90;
  background-color: #f4f4f4;
  overflow: scroll;
}

header {
  background-color: #d13325;
  color: white;
  padding: 15px 0;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  margin-bottom: 20px;
}

nav ul {
  display: flex;
  width: 100%;
  gap: 10px;
  list-style-type: none;
  padding: 0;
  justify-content: center;
}

nav a {
  text-decoration: none;
  color: white;
  padding: 5px 10px;
  border: none;
  background-color: #3a5f90;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

nav a:hover {
  background-color: #2e4b74;
}

.search-box {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.search-box input {
  font-family: 'Press Start 2P', sans-serif;
  padding: 10px 4px;
  font-size: 10px;
  border: 2px solid #ddd;
  border-radius: 4px;
}

.search-box input:hover {
  border: 2px solid #3a5f90;
  outline: none;
}

button {
  font-family: 'Press Start 2P', sans-serif;
  padding: 4px 8px;
  font-size: 10px;
  background-color: #3a5f90;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2e4b74;
}

.pokemon-card {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pokemon-card img {
  max-width: 150px;
  margin-bottom: 20px;
}

.pokedex ul {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 10px;
  overflow-y: auto;
  font-size: 10px;
  overflow: scroll;
}

.pokedex li {
  padding: 10px;
  background-color: #f2f2f2;
  border-radius: 4px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.pokedex img {
  max-width: 80px;
  margin-bottom: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
